.search_container {
  background: #16235b;
  position: absolute;
  top: 80px;
  padding: 10px 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search_input {
  display: flex;
  align-items: center;
  width: 80%;
  background: #fff;
  border-radius: 4px;
}
.search_input input {
  padding: 8px;
  height: 20px;
  line-height: 20px;
  font-size: 14px;
  color: #0d163f;
  width: 100%;
  border: none;
  outline: none;
  border-radius: 0 4px 4px 0;
}
.search_input img {
  padding: 0 10px;
  width: 16px;
}
.search_button {
  width: 38px;
  padding: 0 10px;
  color: #f7f1db;
}
.search_hot_key_container {
  position: absolute;
  top: 160px;
  bottom: 0;
  width: 100%;
  background: #16235b;
}
.search_hot_key_wraper {
  display: flex;
  flex-wrap: wrap;
}
.search_hot_key_item {
  color: #a29e92;
  padding: 0 10px;
  margin: 5px 7px;
  width: auto;
  line-height: 40px;
  background: #0d163f;
  border: 1px solid #0d163f;
  border-radius: 10px;
}
.search_hot_key_wraper>li:nth-child(2) {
  color: #f7f1db;
}
.search_hot_key_title {
  width: 100%;
  text-indent: 0.75em;
  color: #f7f1db;
  margin: 10px;
}
.search_history_container {
  position: absolute;
  top: 160px;
  bottom: 0;
  width: 100%;
  background: #16235b;
  color: #f7f1db;
}
.search_history_title {
  text-align: center;
  line-height: 35px;
}
.search_history_close {
  padding: 0 15px 0 0;
  right: 0;
  position: absolute;
}
.search_history_item {
  display: flex;
  align-items: center;
  border-bottom: 2px solid #0d163f;
  padding: 7px 0;
}
.search_history_item>div:nth-child(1) {
  margin-left: 10px;
}
.search_history_item>div:nth-child(1) img {
  width: 24px;
}
.search_history_item>div:nth-child(2) {
  margin-left: 20px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.search_history_footer {
  text-align: center;
  line-height: 35px;
  color: #a29e92;
}
.search_list_failed {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.search_list_container {
  position: absolute;
  top: 160px;
  bottom: 0;
  width: 100%;
  background: #16235b;
  color: #f7f1db;
}
.search_list_item {
  display: flex;
  align-items: center;
  border-bottom: 2px solid #0d163f;
}
.search_list_item_index {
  padding: 0 20px;
}
.search_list_item_info {
  line-height: 30px;
  margin-left: 15px;
}
.search_list_item_singername {
  color: #a29e92;
}
.search_list_title {
  border-bottom: 2px solid #0d163f;
  display: flex;
}
.search_list_singer_img {
  padding: 5px;
}
.search_list_singer_img img {
  width: 70px;
}
.search_list_singer_info {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin: 5px 0 0 20px;
}
.search_list_singer_info>div:nth-child(1) {
  font-size: 18px;
  font-weight: bold;
}
.search_list_container_close {
  position: absolute;
  right: 0;
  padding: 10px;
}
